.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding-top: 5px;
  .left {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90px;
    height: 100%;
    margin-left: 1.3rem;
    #nav {
      width: 1.5rem;
      height: 1.4rem;
    }
    #glasses {
      width: 31.3px;
      height: 31.3px;
    }
  }
  #logo {
    width: 164px;
    height: 29.97px;
  }
  .right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90px;
    height: 100%;
    margin-right: 1.3rem;
    #heart {
      width: 31.3px;
      height: 31.3px;
    }
    #shoppingBag {
      width: 31.3px;
      height: 31.3px;
    }
  }
}
